[data-animation]::before, [data-animation]::after, [data-animation] {
  opacity: 0; }

[data-animation].animated::before, [data-animation].animated::after, [data-animation].animated {
  opacity: 1; }

.overlay-anim-box2.overlay-anim2::before {
  transform: translateX(100%); }

[data-animation].overlay-anim-box2::before, [data-animation].overlay-anim-box2::after, [data-animation].overlay-anim-box2 {
  opacity: 1; }

.title-fill.title-fill-anim::before, .title-fill.title-fill-anim::after {
  width: 100%; }

[data-animation].title-fill::before, [data-animation].title-fill::after, [data-animation].title-fill {
  opacity: 1; }

.title-fill::before, .title-fill::after {
  content: attr(data-text);
  position: absolute;
  top: 50%;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #ef0d33;
  transform: translateY(-50%);
  left: 0;
  transition: 1s cubic-bezier(0.77, 0, 0.175, 1);
  width: 0%;
  white-space: nowrap;
  overflow: hidden; }
  @media (max-width: 767px) {
    .title-fill::before, .title-fill::after {
      display: none; } }

.title-fill::after {
  color: #fff;
  transition-delay: .3s; }

@media (min-width: 768px) {
  .title-fill {
    color: transparent;
    position: relative;
    display: inline-block; } }

.title-fill.title-fill-anim::before, .title-fill.title-fill-anim::after {
  width: 100%; }

.tr-delay01.title-fill::after {
  transition-delay: .4s !important; }

.fade-anim-box {
  opacity: 0;
  transform: translateY(30px);
  transition: 1s cubic-bezier(0.77, 0, 0.175, 1); }

.fade-anim-box.fade-anim {
  opacity: 1;
  transform: translateY(0); }

.image-zoom {
  overflow: hidden;
  position: relative;
  display: block; }
  .image-zoom:hover .image-zoom__img {
    transform: scale(1.1); }

.image-zoom__img {
  display: block;
  transform: scale(1);
  transition: all 1.5s ease;
  object-fit: contain; }

.reveal-box {
  clip-path: inset(0 100% 0 0);
  transform: translateX(40px);
  transition: 1s; }

.reveal-box.reveal-box-anim {
  clip-path: inset(0 0 0 0);
  transform: translateX(0);
  transition: 1s 0.1s cubic-bezier(0.91, 0.13, 0.26, 0.88); }
